//Mixins
//This mixin must be used to give an element the right elevation color and shadow
//e.g. @include elevation_appearance(1) for elevation level 1
@mixin elevation($level) {
  @if ($level==1) {
    @include mat-elevation(1);
  } @else if ($level==2) {
    @include mat-elevation(2);
  } @else if ($level==3) {
    @include mat-elevation(3);
  } @else if ($level==4) {
    @include mat-elevation(4);
  } @else if ($level==6) {
    @include mat-elevation(6);
  } @else if ($level==8) {
    @include mat-elevation(8);
  } @else if ($level==12) {
    @include mat-elevation(12);
  } @else if ($level==16) {
    @include mat-elevation(16);
  } @else if ($level==24) {
    @include mat-elevation(24);
  }
}

//This mixin must be used to give an element the right elevation color and shadow on hover
//e.g. @include hover(1, $theme) for elevation level 1
@mixin hover($level) {
  @if ($level==1) {
    @include mat-elevation(1);
    background-color: $HoverElevation01;
  } @else if ($level==2) {
    @include mat-elevation(2);
    background-color: $HoverElevation02;
  } @else if ($level==3) {
    @include mat-elevation(3);
    background-color: $HoverElevation03;
  } @else if ($level==4) {
    @include mat-elevation(4);
    background-color: $HoverElevation04;
  } @else if ($level==6) {
    @include mat-elevation(6);
    background-color: $HoverElevation06;
  } @else if ($level==8) {
    @include mat-elevation(8);
    background-color: $HoverElevation08;
  } @else if ($level==12) {
    @include mat-elevation(12);
    background-color: $HoverElevation12;
  } @else if ($level==16) {
    @include mat-elevation(16);
    background-color: $HoverElevation16;
  } @else if ($level==24) {
    @include mat-elevation(24);
    background-color: $HoverElevation24;
  }
}

// create a personalized theme
@function gds-theme($primary, $accent, $warn, $foreground, $background, $IsDark) {
  @return (
    primary: $primary,
    accent: $accent,
    warn: $warn,
    is-dark: $IsDark,
    foreground: $foreground,
    background: $background
  );
}

$mat-ripple-color-opacity: 0.1;

@mixin dashboard-card-component-theme($theme) {
  $primary: map-get($theme, primary);
  $accent: map-get($theme, accent);
  $warn: map-get($theme, warn);
  $foreground: map-get($theme, foreground);
  $background: map-get($theme, background);

  .sioux-card {
    background-color: map-get($background, card);
  }

  .sioux-card-state {
    //background-color: mat-color($accent);
    //background-color: map-get($background, card);

    &.sioux-card-state-done {
      background-color: green;
    }

    &.sioux-card-state-warning {
      background-color: mat-color($warn, 500);
    }

    &.sioux-card-state-error {
      background-color: mat-color($warn);
    }

    &.sioux-card-state-nodata,
    &.sioux-card-state-notvalid {
      background-color: map-get($mat-grey, 700);
    }
  }

  .sioux-card-state-done {
    color: green;
  }

  .sioux-card-state-warning {
    color: mat-color($warn, 500);
  }

  .sioux-card-state-error {
    color: mat-color($warn);
  }

  .sioux-card-state-none {
    color: mat-color($accent);
  }

  .sioux-card-state-nodata,
  .sioux-card-state-notvalid,
  .sioux-card-link,
  .sioux-card-timestamp {
    color: map-get($mat-grey, 700);
  }

  .mat-progress-spinner circle {
    stroke: map-get($mat-grey, 700);
  }
}

//side-nav
@mixin sidenav-component-theme($theme) {
  $primary: map-get($theme, primary);
  $accent: map-get($theme, accent);
  $warn: map-get($theme, warn);

  .active {
    mat-icon {
      color: mat-color($accent);
    }
  }

}

// select-duration
@mixin selection-duration-component-theme($theme) {
  $primary: map-get($theme, primary);
  $accent: map-get($theme, accent);
  $warn: map-get($theme, warn);
  $foreground: map-get($theme, foreground);
  $background: map-get($theme, background);

  .date-selection-duration {
    background-color: map-get($background, background);
    border-radius: 18px !important;
  }

  .mat-button-toggle-appearance-standard {
    background-color: map-get($background, background);
  }
}

$color-list: (
  black: #000,
  white: #fff,
  gray: #828282,
  mischka: #d0d4de,
  silver: #bbb,
  wild-sand: #f5f5f5,
  light-black: rgba(#000, 0.2),
  // colors from nishanth
    hover-teal-bg: #262932,
  focus-teal-bg: #393b43,
  selected-teal-bg: #222736,
  activated-teal-bg: #252b3e,
  activated-teal-text: #7d94d9,
  hover-indigo-bg: #f4f4f4,
  focus-indigo-bg: #dfdfdf,
  selected-indigo-bg: #eef2fa,
  activated-indigo-bg: #e6ebf8,
  activated-indigo-text: #7d94d9,
  background-selected-dark: #222736,
  background-selected-light: #eef2fa,
  amber: #ffbd39,
  // disabled device color
    dark-theme-disabled-device: #393b43,
  light-theme-disabled-device: rgba(black, 0.12),
  light-theme-slide-active: #000,
  light-theme-slide-disbled: #cacaca,
  light-theme-slide-active-cirlce: #cacaca,
  dark-theme-slide-active: #fff,
  dark-theme-slide-disbled: #444d68,
  dark-primary-color: #11151e,
);

@function color($color-name) {
  @return map-get($color-list, $color-name);
}

