@mixin fix-box($fix-para) {
  -webkit-box-sizing: $fix-para;
  -moz-box-sizing: $fix-para;
}

@mixin font-size-height-spacing-setup($font-size, $line-height, $letter-spacing) {
  font-size: $font-size;
  line-height: $line-height;
  letter-spacing: $letter-spacing;
}

@mixin box-shadow() {
  -webkit-box-shadow: 0 2px 4px 0.5px rgba(0, 0, 0, 0.16);
  -moz-box-shadow: 0 2px 4px 0.5px rgba(0, 0, 0, 0.16);
  box-shadow: 0 2px 4px 0.5px rgba(0, 0, 0, 0.16);
}
