//------- SNACKBAR -----------------------------------------------------------------
.mat-snack-bar-container {
  @include mat-elevation(6);
  color: $TextLight;
  background: $SnackbarColor;
}

.mat-simple-snackbar-action > .mat-button {
  color: $TextAccentColorLight;

  &:hover {
    background-color: transparent;
  }
}
