//SidepanelCloserSettings------------------------------------------------------------------------------
mat-sidenav > div > sioux-sidepanel > .sidepanel-closer.mat-button,
.sidepanel-closer {
  background-color: $SidepanelCloser;
  min-width: $SidepanelCloserMinWidth;
  position: absolute;
  height: 100% !important;
  top: 0;
  border: 0 !important;
  z-index: 1;

  // define width dependent on device type (mouse or touch)
  //device with mouse
  @media (any-hover: hover) {
    width: $SidepanelCloserWidthMouse;

    .mat-icon {
      font-size: $SidepanelCloserWidthMouse !important;
    }
  }

  //touch device
  @media (any-hover: none), (any-hover: on-demand) {
    width: $SidepanelCloserWidthTouch;

    .mat-icon {
      font-size: $SidepanelCloserWidthTouch !important;
    }
  }

  .mat-icon {
    position: absolute;
    color: $Text;
    left: 0;
    font-weight: bold;
  }

  &:hover {
    background-color: $SidepanelCloserHover;
  }

  &:focus {
    background-color: $SidepanelCloserHover;
  }
}

//SidePanelCloser settings for sidenav of type over
mat-sidenav.mat-drawer-over > div > sioux-sidepanel > .sidepanel-closer.mat-button {
  background-color: $SidepanelCloserOver;

  &:hover {
    background-color: $HoverSidePanelCloserOver;
  }

  &:focus {
    background-color: $HoverSidePanelCloserOver;
  }
}

//General side nav settings ------------------------------------------------------------------

//state push and side
.mat-drawer-end {
  max-width: 320px;
}

.mat-drawer-backdrop {
  background-color: $Elevation00_10 !important;
}

//on navigation drawer/side sheet of type side/push ---------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-side {
  @include mat-elevation(1);
  background-color: $Elevation01; //!important;
  border: 0 !important;

  .sioux-sidepanel {
    @include mat-elevation(1);
    background-color: $Elevation01 !important;
  }
}

//on navigation drawer/side sheet of type over----------------------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-over {
  @include mat-elevation(16);
  background-color: $Elevation16;

  .sioux-sidepanel {
    @include mat-elevation(16);
    background-color: $Elevation16;
  }
}
