.mat-expansion-panel,
.mat-expansion-panel-header {
  background-color: $Elevation01;
}

.mat-expansion-panel:not(.mat-expanded) .mat-expansion-panel-header {
  &:not([aria-disabled='true']) {
    &.cdk-keyboard-focused,
    &.cdk-program-focused,
    &:hover {
      background-color: $HoverElevation01;
    }
  }
}

//on navigation drawer/side sheet of type side/push ---------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-side {
  .mat-expansion-panel,
  .mat-expansion-panel-header {
    background-color: $Elevation01;
  }

  .mat-expansion-panel:not(.mat-expanded) .mat-expansion-panel-header {
    &:not([aria-disabled='true']) {
      &.cdk-keyboard-focused,
      &.cdk-program-focused,
      &:hover {
        background-color: $HoverElevation01;
      }
    }
  }
}

//on navigation drawer/side sheet of type over----------------------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-over {
  .mat-expansion-panel,
  .mat-expansion-panel-header {
    background-color: $Elevation16;
  }

  .mat-expansion-panel:not(.mat-expanded) .mat-expansion-panel-header {
    &:not([aria-disabled='true']) {
      &.cdk-keyboard-focused,
      &.cdk-program-focused,
      &:hover {
        background-color: $HoverElevation16;
      }
    }
  }
}

//on dialog---------------------------------------------------------------------------------------------------------------------------------
.mat-dialog-content {
  .mat-expansion-panel,
  .mat-expansion-panel-header {
    background-color: $Elevation24;
  }

  .mat-expansion-panel:not(.mat-expanded) .mat-expansion-panel-header {
    &:not([aria-disabled='true']) {
      &.cdk-keyboard-focused,
      &.cdk-program-focused,
      &:hover {
        background-color: $HoverElevation24;
      }
    }
  }
}
