//------- PROGRESS -----------------------------------------------------------------
.mat-progress-bar-fill::after {
  background: $AccentColor;
}

.mat-progress-bar-buffer {
  background-color: $ProgressBackground;
}

.mat-progress-bar-background {
  background-color: $ProgressBackground;
  fill: $ProgressBackgroundFill;
}

.mat-progress-spinner circle,
.mat-spinner circle {
  stroke: $AccentColor;
}
