
//general button settings
.mat-button .mat-icon,
a.mat-button .mat-icon,
a.mat-raised-button .mat-icon,
a.mat-flat-button .mat-icon,
a.mat-stroked-button .mat-icon {
  display: flex;
  vertical-align: middle;
  justify-content: center;
  height: 1em;
  width: 1em;
}

@mixin SetPrimaryButtonColors($PrimaryButtonColor, $PrimaryButtonHover, $PrimaryButtonFocused, $PrimaryButtonDisabled) {
  //defintions for primary buttons (used for Raised and Flat buttons)
  .mat-primary.mat-raised-button,
  .mat-raised-button,
  .mat-primary.mat-flat-button,
  .mat-flat-button,
  .mat-primary.mat-fab,
  .mat-primary.mat-mini-fab {
    background-color: $PrimaryButtonColor;
    color: $Text;

    &:hover {
      background-color: $PrimaryButtonHover;
      color: $Text;
    }

    &:focus {
      background-color: $PrimaryButtonFocused;
      color: $Text;
      border: 1px solid $Text !important;
    }

    &:disabled {
      background: $PrimaryButtonDisabled;
      color: $TextDisabled;

      .mat-icon {
        color: $TextDisabled;
      }
    }
  }

  //  BasicPrimaryButton
  .mat-primary.mat-button,
  .mat-button {
    color: $Text;
    background: transparent;

    &:hover {
      background-color: $PrimaryButtonHover;
      cursor: pointer;
      color: $Text;
    }

    &:focus {
      background-color: $PrimaryButtonFocused;
      color: $Text;
    }

    &:disabled {
      color: $TextDisabled;
      background-color: transparent;

      .mat-icon {
        color: $TextDisabled;
      }
    }
  }

  //Defintions for Stroked buttons

  // 1) PrimaryStrokedButton
  .mat-primary.mat-stroked-button,
  .mat-stroked-button {
    color: $Text;
    border: 1px solid $Text !important;

    &:hover {
      color: $Text;
      background-color: $StrokedButtonHover;
      border: 1px solid $Text !important;
    }

    &:focus {
      color: $Text;
      border: 1px solid $Text !important;
      background-color: $StrokedButtonFocus;
    }

    &:disabled {
      color: $TextDisabled;
      border: 1px solid $TextDisabled !important;
      background-color: transparent;

      .mat-icon {
        color: $TextDisabled;
      }
    }
  }
}

//--------component specific CSS---------------------------------------------------------------------------------------------------------------------

.mat-button {
  font-size: 14px;
  height: 36px;
  min-width: 64px;
  padding-left: 16px;
  padding-right: 16px;
  outline: none;
  border-width: 0;
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
}

//--------Accent Buttons ---------------------------------------------------------------

//defintions for Accent buttons (used for Raised and Flat buttons)
.mat-accent.mat-raised-button,
.mat-accent.mat-flat-button,
.mat-accent.mat-fab,
.mat-accent.mat-mini-fab {
  background-color: $AccentColor;
  color: $TextLight;

  &:hover {
    background-color: $AccentHover;
    color: $TextLight;
  }

  &:focus {
    background-color: $AccentFocused;
    color: $TextLight;
    border: 1px solid $Text !important;
  }

  &:disabled {
    background: $AccentDisabled;
    color: $TextDisabled;

    .mat-icon {
      color: $TextDisabled;
    }
  }

  .mat-icon,
  .mat-icon-no-color {
    color: $TextLight !important;
  }
}

//Defintions for basic Buttons
.mat-accent.mat-button,
.mat-button {
  color: $TextAccentColor;
  background: transparent;

  &:hover {
    background-color: $AccentHover;
    cursor: pointer;
    color: $TextLight;
  }

  &:focus {
    background-color: $AccentFocused;
    color: $TextLight;
  }

  &:disabled {
    color: $TextAccentDisabled;
    background-color: transparent;

    .mat-icon {
      color: $TextDisabled;
    }
  }
}

// 2) AccentStrokedButton
.mat-accent.mat-stroked-button {
  color: $TextAccentColor;
  background-color: transparent;
  border: 1px solid $TextAccentColor !important;

  &:hover {
    color: $TextAccentColor;
    background-color: $AccentColor10;
    border: 1px solid $TextAccentColor !important;
  }

  &:focus {
    background-color: $AccentColor20;
    color: $TextAccentColor;
    border: 1px solid $TextAccentColor !important;
  }

  &:disabled {
    color: $TextAccentColor38;
    background-color: transparent;
    border: 1px solid $TextAccentColor38 !important;

    .mat-icon {
      color: $TextDisabled;
    }
  }
}

.mat-icon-button {
  &:hover {
    background-color: $HoverElevation01;
  }

  &:disabled {
    color: $TextDisabled;
    background-color: transparent;
  }
}

//Specific definition for fabs on dark map
button.PrimaryFabOnMap {
  background-color: $PrimaryFabOnMap !important;
  color: $TextWhite;

  &:hover {
    background-color: $HoverPrimaryFabOnMap !important;
    color: $TextWhite;
  }

  &:focus {
    background-color: $FocusedPrimaryFabOnMap !important;
    color: $TextWhite;
    border: 1px solid $TextWhite !important;
  }

  &:disabled {
    background: $PrimaryDisabled;
    color: $TextDisabled;
  }
}

//--------------------------------Primary Buttons ----------------------------------------------------------------------------

//On Background and elevation 01
@include SetPrimaryButtonColors(
  $PrimaryButtonColor01,
  $PrimaryButtonHover01,
  $PrimaryButtonFocus01,
  $PrimaryButtonDisabled01
);

//on navigation drawer/side sheet of type side/push ---------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-side {
  //PrimaryButtons
  @include SetPrimaryButtonColors(
    $PrimaryButtonColor01,
    $PrimaryButtonHover01,
    $PrimaryButtonFocus01,
    $PrimaryButtonDisabled01
  );
}

//Within a table on elevation 08------------------------------------------------------------------------------------
.mat-table {
  .mat-row {
    @include SetPrimaryButtonColors(
      $PrimaryButtonColor08,
      $PrimaryButtonHover08,
      $PrimaryButtonFocus08,
      $PrimaryButtonDisabled08
    );
  }
}

//within a textfield
.mat-form-field {
  @include SetPrimaryButtonColors(
    $PrimaryButtonColor16,
    $PrimaryButtonHover16,
    $PrimaryButtonFocus16,
    $PrimaryButtonDisabled16
  );
}

//on navigation drawer/side sheet of type over----------------------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-over {
  //PrimaryButtons
  @include SetPrimaryButtonColors(
    $PrimaryButtonColor16,
    $PrimaryButtonHover16,
    $PrimaryButtonFocus16,
    $PrimaryButtonDisabled16
  );

  .mat-table {
    .mat-row {
      //PrimaryButtons
      @include SetPrimaryButtonColors(
        $PrimaryButtonColorOnDialog,
        $PrimaryButtonHoverOnDialog,
        $PrimaryButtonFocusOnDialog,
        $PrimaryButtonDisabledOnDialog
      );
    }
  }
}

//on dialog---------------------------------------------------------------------------------------------------------------------------------
.mat-dialog-content {
  @include SetPrimaryButtonColors(
    $PrimaryButtonColorOnDialog,
    $PrimaryButtonHoverOnDialog,
    $PrimaryButtonFocusOnDialog,
    $PrimaryButtonDisabledOnDialog
  );

  //table definitions
  .mat-table {
    .mat-row {
      //PrimaryButtons
      @include SetPrimaryButtonColors(
        $PrimaryButtonColorOnDialog,
        $PrimaryButtonHoverOnDialog,
        $PrimaryButtonFocusOnDialog,
        $PrimaryButtonDisabledOnDialog
      );
      background-color: $ElevationOnDialog !important;
    }
  }

  .mat-dialog-actions {
    @include SetPrimaryButtonColors(
      $PrimaryButtonColorOnDialog,
      $PrimaryButtonHoverOnDialog,
      green,
      $PrimaryButtonDisabledOnDialog
    );
  }
}
