//------- TOOLBAR -----------------------------------------------------------------
@media screen and (min-width: 960px) {
  .mat-toolbar {
    z-index: 50;
  }
}

@media screen and (min-width: 599px) {
  .mat-toolbar {
    z-index: 0;
  }
}

.mat-toolbar {
  position: relative;
  z-index: 50;

  //required for scrolling
  @if (mat-elevation-z4) {
    @include elevation(4);
  }

  .mat-icon-button {
    &:hover {
      background-color: $HoverElevation08;
    }
  }
}
