.tableGrid {
  // span table over complete width
  @include mat-elevation(8);
  width: 100%;

  //Add column separator lines
  .mat-cell {
    border-left: 1px solid $TextDisabled;
    cursor: text;

    &:first-of-type {
      border-left: 0 solid $TextDisabled;
    }
  }

  .disabledTableGridCell {
    background-color: $TableCellDisabled01;
    color: $TextTableCellDisabled01;
    cursor: default;

    &:hover {
      background-color: $HoverTableCellDisabled01;
      color: $TextTableCellDisabled01;
    }
  }

  .disabledTableGridCell .highlighted {
    background-color: $SelectedTableCellDisabled01 !important;
  }
}

//on navigation drawer/side sheet of type over----------------------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-over {
  .disabledTableGridCell {
    background-color: $TableCellDisabled16;
    color: $TextTableCellDisabled16;

    &:hover {
      background-color: $HoverTableCellDisabled16;
      color: $TextTableCellDisabled16;
    }
  }

  .disabledTableGridCell .highlighted {
    background-color: $SelectedTableCellDisabled16 !important;
  }
}

//on dialog---------------------------------------------------------------------------------------------------------------------------------
.mat-dialog-content {
  .disabledTableGridCell {
    background-color: $TableCellDisabledOnDialog;
    color: $TextTableCellDisabledOnDialog;

    &:hover {
      background-color: $HoverTableCellDisabledOnDialog;
      color: $TextTableCellDisabledOnDialog;
    }
  }

  .disabledTableGridCell .highlighted {
    background-color: $SelectedTableCellDisabledOnDialog !important;
  }
}
