
//switch on (accent)
.mat-slide-toggle.mat-accent.mat-checked .mat-slide-toggle-thumb {
  background-color: $AccentColor;
}

.mat-slide-toggle.mat-accent.mat-checked .mat-slide-toggle-bar {
  background-color: $AccentDisabled;
}

//switch on (primary)

.mat-slide-toggle.mat-primary.mat-checked .mat-slide-toggle-thumb {
  background-color: $PrimaryButtonColor01;
}

.mat-slide-toggle.mat-primary.mat-checked .mat-slide-toggle-bar {
  background-color: rgba($PrimaryButtonColor01, 0.38);
}

//switch on (warn)

$WarningColor: mat-color($warn);

.mat-slide-toggle.mat-warn.mat-checked .mat-slide-toggle-thumb {
  background-color: $WarningColor;
}

.mat-slide-toggle.mat-warn.mat-checked .mat-slide-toggle-bar {
  background-color: rgba($WarningColor, 0.38);
}

.mat-slide-toggle-thumb {
  background-color: $SlideToggleThumb;
}

.mat-slide-toggle-bar {
  background-color: $TextDisabled;
}

//on navigation drawer/side sheet of type side----------------------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-side,
.mat-card {
  //switch on (primary)
  .mat-slide-toggle.mat-primary.mat-checked .mat-slide-toggle-thumb {
    background-color: $PrimaryButtonColor08;
  }

  .mat-slide-toggle.mat-primary.mat-checked .mat-slide-toggle-bar {
    background-color: rgba($PrimaryButtonColor08, 0.38);
  }
}

//on navigation drawer/side sheet of type over----------------------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-over {
  //switch on (primary)

  .mat-slide-toggle.mat-primary.mat-checked .mat-slide-toggle-thumb {
    background-color: $PrimaryButtonColor16;
  }

  .mat-slide-toggle.mat-primary.mat-checked .mat-slide-toggle-bar {
    background-color: rgba($PrimaryButtonColor16, 0.38);
  }
}

//on dialog---------------------------------------------------------------------------------------------------------------------------------
.mat-dialog-content {
  //switch on (primary)

  .mat-slide-toggle.mat-primary.mat-checked .mat-slide-toggle-thumb {
    background-color: $PrimaryButtonColor24;
  }

  .mat-slide-toggle.mat-primary.mat-checked .mat-slide-toggle-bar {
    background-color: rgba($PrimaryButtonColor24, 0.38);
  }
}
