.mat-table {
  // span table over complete width
  @include mat-elevation(8);
  width: 100%;

  .mat-header-cell {
    background-color: $ElevationOnDialog !important;
    font-size: 16px;
    font-weight: bold;
    padding-left: 16px !important;
    padding-right: 16px !important;
  }

  .mat-cell {
    padding-left: 16px !important;
    padding-right: 16px !important;
    white-space: nowrap;
    cursor: default;

    &:first-of-type {
      padding-left: 16px;
    }

    &:last-of-type {
      padding-right: 16px;
    }
  }

  .mat-row {
    //PrimaryButtons
    @include SetPrimaryButtonColors(
      $PrimaryButtonColor08,
      $PrimaryButtonHover08,
      $PrimaryButtonPressed08,
      $PrimaryButtonDisabled08
    );
    background-color: $Elevation08 !important;
  }

  .mat-row.hovered {
    background-color: $HoverElevation08 !important;
  }

  .mat-row.highlighted {
    background-color: $SelectedElevation08 !important;
  }
}

//required to overwrite the horizontal separtion line in tables
mat-row,
mat-header-row,
mat-footer-row,
th.mat-header-cell,
td.mat-cell,
td.mat-footer-cell {
  border-bottom: $TextDisabled;
}

//on navigation drawer/side sheet of type side/push ---------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-side {
  //table definitions
  .mat-table {
    .mat-header-cell {
      background-color: $ElevationOnDialog !important;
    }

    .mat-row {
      background-color: $Elevation08 !important;
    }
  }
}

//on navigation drawer/side sheet of type over----------------------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-over {
  //table definitions
  .mat-table {
    .mat-header-cell {
      background-color: $HeaderOnDialog !important;
    }

    .mat-row {
      //PrimaryButtons
      @include SetPrimaryButtonColors(
        $PrimaryButtonColorOnDialog,
        $PrimaryButtonHoverOnDialog,
        $PrimaryButtonPressedOnDialog,
        $PrimaryButtonDisabledOnDialog
      );
      background-color: $ElevationOnDialog !important;
    }

    .mat-row.hovered {
      background: $HoverElevationOnDialog !important;
    }

    .mat-row.highlighted {
      background: $SelectedElevationOnDialog !important;
    }
  }
}

//on dialog---------------------------------------------------------------------------------------------------------------------------------
.mat-dialog-content {
  //table definitions
  .mat-table {
    .mat-header-cell {
      background-color: $HeaderOnDialog !important;
    }

    .mat-row {
      //PrimaryButtons
      @include SetPrimaryButtonColors(
        $PrimaryButtonColorOnDialog,
        $PrimaryButtonHoverOnDialog,
        $PrimaryButtonPressedOnDialog,
        $PrimaryButtonDisabledOnDialog
      );
      background-color: $ElevationOnDialog !important;
    }

    .mat-row.hovered {
      background: $HoverElevationOnDialog !important;
    }

    .mat-row.highlighted {
      background: $SelectedElevationOnDialog !important;
    }
  }
}
