@mixin SetPrimaryChipColors($PrimaryChipColor, $PrimaryChipHover, $PrimaryChipActivated, $PrimaryChipDisabled) {
  .mat-chip.mat-standard-chip {
    background-color: $PrimaryChipColor;
    color: $Text;

    &:not(.mat-chip-disabled) {
      &:active {
        background-color: $PrimaryChipActivated;
        color: $TextAccentColor;
      }
    }

    &.mat-chip-disabled {
      background: $PrimaryChipDisabled;
      color: $TextDisabled;
    }
  }

  .mat-chip.mat-standard-chip.mat-chip-selected {
    &.mat-primary {
      background-color: $PrimaryButtonActivated01;
      color: $TextAccentColor;
    }
  }

  //defintions for primary chips
  .mat-chip.mat-standard-chip {
    background-color: $PrimaryChipColor;
    color: $Text;

    &:hover {
      background-color: $PrimaryChipHover;
      color: $Text;
    }

    &:disabled {
      background: $PrimaryChipDisabled;
      color: $TextDisabled;

      .mat-icon {
        color: $TextDisabled;
      }
    }
  }
}

.mat-chip.mat-standard-chip.mat-primary:focus {
  background-color: $PrimaryButtonFocus01;
  color: $Text;
}

.mat-chip.mat-standard-chip.mat-chip-selected.mat-accent {
  background: $AccentSelected;
  color: $TextLight;
}

.mat-chip.mat-standard-chip.cdk-drag-preview.mat-primary {
  background-color: $PrimaryButtonDragged01;
}

.mat-chip.mat-standard-chip.cdk-drag-preview.mat-accent {
  background-color: $AccentDragged;
}

//--------------------------------Primary Buttons ----------------------------------------------------------------------------

//On Background and elevation 01
@include SetPrimaryChipColors(
  $PrimaryButtonColor01,
  $PrimaryButtonHover01,
  $PrimaryButtonActivated01,
  $PrimaryButtonDisabled01
);

//on navigation drawer/side sheet of type side/push ---------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-side {
  //PrimaryButtons
  @include SetPrimaryChipColors(
    $PrimaryButtonColor01,
    $PrimaryButtonHover01,
    $PrimaryButtonActivated01,
    $PrimaryButtonDisabled01
  );

  .mat-chip.mat-standard-chip.mat-primary:focus {
    background-color: $PrimaryButtonFocus01;
  }

  .mat-chip.mat-standard-chip.cdk-drag-preview.mat-primary {
    background-color: $PrimaryButtonDragged01;
  }
}
//Within a table on elevation 08------------------------------------------------------------------------------------
.mat-table {
  .mat-row {
    @include SetPrimaryChipColors(
      $PrimaryButtonColor08,
      $PrimaryButtonHover08,
      $PrimaryButtonActivated08,
      $PrimaryButtonDisabled08
    );

    .mat-chip.mat-standard-chip.mat-primary:focus {
      background-color: $PrimaryButtonFocus08;
    }

    .mat-chip.mat-standard-chip.cdk-drag-preview.mat-primary {
      background-color: $PrimaryButtonDragged08;
    }
  }
}

//within a textfield
.mat-form-field {
  @include SetPrimaryChipColors(
    $PrimaryButtonColor16,
    $PrimaryButtonHover16,
    $PrimaryButtonActivated16,
    $PrimaryButtonDisabled16
  );

  .mat-chip.mat-standard-chip.mat-primary:focus {
    background-color: $PrimaryButtonFocus16;
  }

  .mat-chip.mat-standard-chip.cdk-drag-preview.mat-primary {
    background-color: $PrimaryButtonDragged16;
  }
}
//on navigation drawer/side sheet of type over----------------------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-over {
  //PrimaryButtons
  @include SetPrimaryChipColors(
    $PrimaryButtonColor16,
    $PrimaryButtonHover16,
    $PrimaryButtonActivated16,
    $PrimaryButtonDisabled16
  );

  .mat-chip.mat-standard-chip.cdk-drag-preview.mat-primary {
    background-color: $PrimaryButtonDragged16;
  }

  .mat-chip.mat-standard-chip.mat-primary:focus {
    background-color: $PrimaryButtonFocus16;
  }

  .mat-table {
    .mat-row {
      //PrimaryButtons
      @include SetPrimaryChipColors(
        $PrimaryButtonColorOnDialog,
        $PrimaryButtonHoverOnDialog,
        $PrimaryButtonActivatedOnDialog,
        $PrimaryButtonDisabledOnDialog
      );

      .mat-chip.mat-standard-chip.mat-primary:focus {
        background-color: $PrimaryButtonFocusOnDialog;
      }

      .mat-chip.mat-standard-chip.cdk-drag-preview.mat-primary {
        background-color: $PrimaryButtonDraggedOnDialog;
      }
    }
  }
}

//on dialog---------------------------------------------------------------------------------------------------------------------------------
.mat-dialog-content {
  @include SetPrimaryChipColors(
    $PrimaryButtonColor24,
    $PrimaryButtonHover24,
    $PrimaryButtonActivated24,
    $PrimaryButtonDisabled24
  );

  .mat-chip.mat-standard-chip.mat-primary:focus {
    background-color: $PrimaryButtonFocusOnDialog;
  }

  .mat-chip.mat-standard-chip.cdk-drag-preview.mat-primary {
    background-color: $PrimaryButtonDraggedOnDialog;
  }

  //table definitions
  .mat-table {
    .mat-row {
      //PrimaryButtons
      @include SetPrimaryChipColors(
        $PrimaryButtonColorOnDialog,
        $PrimaryButtonHoverOnDialog,
        $PrimaryButtonActivatedOnDialog,
        $PrimaryButtonDisabledOnDialog
      );

      background-color: $ElevationOnDialog !important;

      .mat-chip.mat-standard-chip.mat-primary:focus {
        background-color: $PrimaryButtonFocusOnDialog;
      }

      .mat-chip.mat-standard-chip.cdk-drag-preview.mat-primary {
        background-color: $PrimaryButtonDraggedOnDialog;
      }
    }
  }
}
