//The wizard control is called mat-stepper in angular.
.mat-step-header {
  &:hover {
    background-color: $HoverElevation01;
  }

  &.cdk-keyboard-focused,
  &.cdk-program-focused {
    background-color: $FocusedElevation01;
  }

  // On touch devices the :hover state will linger on the element after a tap.
  // Reset it via `@media` after the declaration, because the media query isn't
  // supported by all browsers yet.
  @media (hover: none) {
    &:hover {
      background: none;
    }
  }

  .mat-step-label,
  .mat-step-optional {
    color: $TextSecondary;
  }

  .mat-step-label.mat-step-label-active {
    color: $Text;
  }

  .mat-step-label.mat-step-label-error {
    color: mat-color($warn);
  }
}

//Don't spread the steps over the complete width
.mat-stepper-horizontal-line {
  flex: 0 !important;
}

//enlarge the size of the icons for desktop version
.mat-stepper-horizontal .mat-step-header {
  .mat-step-icon {
    height: 32px;
    width: 32px;
  }

  .mat-step-icon-selected,
  .mat-step-icon-state-done,
  .mat-step-icon-state-edit {
    height: 32px;
    width: 32px;
  }

  .mat-step-icon-state-error {
    height: 32px;
    width: 32px;
  }
}

.mat-stepper-horizontal,
.mat-stepper-vertical {
  background-color: $Elevation01;
}

.mat-stepper-vertical-line::before {
  border-left-color: $TextDisabled;
}

.mat-horizontal-stepper-header::before,
.mat-horizontal-stepper-header::after,
.mat-stepper-horizontal-line {
  border-top-color: $TextDisabled;
}

// Icon states
.mat-step-header .mat-step-icon {
  background-color: $PrimaryButtonHover01;
  color: $Text;
}

.mat-step-header .mat-step-icon.mat-step-icon-selected {
  background-color: $AccentColor;
  color: $Text;
  border: 2px solid $Text;
}

.mat-step-header .mat-step-icon.mat-step-icon-state-done,
.mat-step-header .mat-step-icon.mat-step-icon-state-edit {
  background-color: $AccentColor;
  color: $Text;
}

.mat-step-header
.mat-step-icon.mat-step-icon-state-error {
  background-color: mat-color($warn);
  color: $TextLight;
}

//on navigation drawer/side sheet of type side/push ---------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-side,
.mat-card {
  .mat-step-header {
    &:hover {
      background-color: $HoverElevation08;
    }

    &.cdk-keyboard-focused,
    &.cdk-program-focused {
      background-color: $FocusedElevation08;
    }
  }

  .mat-step-icon {
    background-color: $PrimaryButtonHover08;
    color: $Text;
  }

  .mat-stepper-horizontal,
  .mat-stepper-vertical {
    background-color: $Elevation08;

    .mat-form-field-appearance-fill {
      .mat-form-field-flex {
        background-color: $Elevation24 !important;

        .mat-icon-button {
          &:hover {
            background-color: $HoverElevation24;
          }
        }
      }
    }
  }
}

//on navigation drawer/side sheet of type over----------------------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-over {
  .mat-step-header {
    &:hover {
      background-color: $HoverElevationOnDialog;
    }

    &.cdk-keyboard-focused,
    &.cdk-program-focused {
      background-color: $FocusedElevationOnDialog;
    }
  }

  .mat-step-icon {
    background-color: $PrimaryButtonHoverOnDialog;
    color: $Text;
  }

  .mat-stepper-horizontal,
  .mat-stepper-vertical {
    background-color: $ElevationOnDialog;

    .mat-form-field-appearance-fill {
      .mat-form-field-flex {
        background-color: $HeaderOnDialog !important;

        .mat-icon-button {
          &:hover {
            background-color: $HoverHeaderOnDialog;
          }
        }
      }
    }
  }
}

//on dialog---------------------------------------------------------------------------------------------------------------------------------
.mat-dialog-content {
  .mat-step-header {
    &:hover {
      background-color: $HoverElevationOnDialog;
    }

    &.cdk-keyboard-focused,
    &.cdk-program-focused {
      background-color: $FocusedElevationOnDialog;
    }
  }

  .mat-step-icon {
    background-color: $PrimaryButtonHoverOnDialog;
    color: $Text;
  }

  .mat-stepper-horizontal,
  .mat-stepper-vertical {
    background-color: $ElevationOnDialog;

    .mat-form-field-appearance-fill {
      .mat-form-field-flex {
        background-color: $HeaderOnDialog !important;

        .mat-icon-button {
          &:hover {
            background-color: $HoverHeaderOnDialog;
          }
        }
      }
    }
  }
}

