@import './../common/theming';

.mat-icon {
  vertical-align: middle;
}

$functionalColors: getFunctionalColors($theme);
$FunctionalRed: map-get($functionalColors, functionalRed);
$FunctionalYellow: map-get($functionalColors, functionalYellow);
$FunctionalBlue: map-get($functionalColors, functionalBlue);

.warning {
  color: $FunctionalYellow;
}

.error {
  color: $FunctionalRed;
}

.information {
  color: $FunctionalBlue;
}
