// custom background and foreground palettes
//raised-button: background color of the basic raised button
$gds-dark-theme-background: (
  status-bar: $StatusBar,
  app-bar: $AppBar,
  background: $PageBackground,
  hover: $PrimaryHover,
  card: $Elevation01,
  raised-button: $AccentColor,
  focused-button: $AccentPressed,
  selected-button: $AccentSelected,
  selected-disabled-button: $AccentDisabled,
  disabled-button-toggle: $AccentDisabled,
  unselected-chip: $AccentColor,
  disabled-list-option: $AccentDisabled,
  background01: $Background01,
  background02: $Background02,
  background03: $Background03,
  background04: $Background04,
  background06: $Background06,
  background08: $Background08,
  background12: $Background12,
  background16: $Background16,
  background24: $Background24,
  backgroundOnDialog: $BackgroundOnDialog,
  elevation01: $Elevation01,
  elevation02: $Elevation02,
  elevation03: $Elevation03,
  elevation04: $Elevation04,
  elevation06: $Elevation06,
  elevation08: $Elevation08,
  elevation12: $Elevation12,
  elevation16: $Elevation16,
  elevation24: $Elevation24,
  elevationOnDialog: $ElevationOnDialog,
  hoverElevation01: $HoverElevation01,
  hoverElevation02: $HoverElevation02,
  hoverElevation03: $HoverElevation03,
  hoverElevation04: $HoverElevation04,
  hoverElevation06: $HoverElevation06,
  hoverElevation08: $HoverElevation08,
  hoverElevation12: $HoverElevation12,
  hoverElevation16: $HoverElevation16,
  hoverElevation24: $HoverElevation24,
  accentHover: $AccentHover,
  accentFocused: $AccentFocused,
  accentSelected: $AccentSelected,
  accentActivated: $AccentActivated,
  accentPressed: $AccentPressed,
  accentDragged: $AccentDragged,
  accentDisabled: $AccentDisabled,
  primaryHover: $PrimaryHover,
  primaryFocus: $PrimaryFocus,
  primarySelected: $PrimarySelected,
  primaryActivated: $PrimaryActivated,
  primaryPressed: $PrimaryPressed,
  primaryDragged: $PrimaryDragged,
  primaryDisabled: $AccentDisabled,
);

$gds-dark-theme-foreground: (
  base: $AccentColor,
  //also used for the border of stroked buttons and right divider in the navigation drawer
  divider: $TextDisabled,
  dividers: $TextDisabled,
  disabled: $AccentDisabled,
  disabled-button: $AccentDisabled,
  disabled-text: $TextAccentDisabled,
  hint-text: $TextDisabled,
  secondary-text: $TextSecondary,
  icon: $IconColor,
  icons: $IconColor,
  text: $Text,
  slider-min: $Text,
  slider-off: $TextDisabled,
  slider-off-active: $TextDisabled,
  textAccentColor: $TextAccentColor,
  textAccentDisabled: $TextAccentDisabled,
  textDisabled: $TextDisabled,
  singleLineMv: $SingleLineMV,
  singleLineLv: $SingleLineLV,
);

$TooltipText: black; // same as $Text light theme
$TooltipTextAccentColor: #395fc6; // same as $TextAccentColor of light theme; used for snackbar, FAB, tooltip
$TooltipBackground: white;

//my dark theme definition
// If the palette gets no hue value, 500 is used per default. Otherwise the given value.
//values to give: Normal, light, dark
$primary: mat-palette($PrimaryColorDark, 500, 600, 700);
$accent: mat-palette($AccentColorTable, 500, 600, 700);
$warn: mat-palette($WarningColor);

$theme: gds-theme($primary, $accent, $warn, $gds-dark-theme-foreground, $gds-dark-theme-background, true);
@include angular-material-theme($theme);
@include scrollbar($Elevation16, $HoverElevationOnDialog, $HoverElevationOnDialog);
