@import '~@angular/material/theming';

$SidepanelCloserWidthMouse: 16px;
$SidepanelCloserWidthTouch: 12px;
$SidepanelCloserMinWidth: 4px;
$ScrollbarWidthMouse: 12px;
$ScrollbarWidthTouch: 8px;

//provides all supporting colors in the correct order
@function getSupportingColors() {
  @return (
    supportingColor01: #1de6e6,
    supportingColor02: #e1197d,
    supportingColor03: #1ad075,
    supportingColor04: #9c4dea,
    supportingColor05: #ffbd57,
    supportingColor06: #75d01a,
    supportingColor07: #ef7171,
    supportingColor08: #1d81e6,
    supportingColor09: #d741a7,
    supportingColor10: #7373ef
  );
}

//Functional colors
@function getFunctionalColors($theme) {
  //retrieve base for functional oldData and no data
  $foreground: map-get($theme, foreground);
  $baseColor: map-get($foreground, text);
  $FunctionalRed: #ff3131;
  $FunctionalBlue: #0591da;
  $FunctionalGreen: #27aa4c;
  $FunctionalYellow: #ffd200;
  $FunctionalOldData: rgba($baseColor, 0.65);
  $FunctionalNoData: rgba($baseColor, 0.35);
  @return (
    functionalRed: $FunctionalRed,
    functionalBlue: $FunctionalBlue,
    functionalGreen: $FunctionalGreen,
    functionalYellow: $FunctionalYellow,
    functionalOldData: $FunctionalOldData,
    functionalNoData: $FunctionalNoData
  );
}

$fontName: 'Siemens Sans';

//defintions from angular except the font
$sioux-typography: mat-typography-config(
  $font-family: $fontName,
  $display-4: mat-typography-level(112px, 112px, 300),
  $display-3: mat-typography-level(56px, 56px, 400),
  $display-2: mat-typography-level(45px, 48px, 400),
  $display-1: mat-typography-level(34px, 40px, 400),
  $headline: mat-typography-level(24px, 32px, 400),
  $title: mat-typography-level(20px, 32px, 500),
  $subheading-2: mat-typography-level(16px, 28px, 400),
  $subheading-1: mat-typography-level(15px, 24px, 400),
  $body-2: mat-typography-level(14px, 24px, 500),
  $body-1: mat-typography-level(14px, 20px, 400),
  $caption: mat-typography-level(12px, 20px, 400),
  $button: mat-typography-level(14px, 14px, 500),
  $input: mat-typography-level(inherit, 1.125, 400),
);

$config: mat-typography-config();

//font mixins for user defined components
@mixin sioux-card-typography($config) {
  .sioux-card {
    font-family: mat-font-family($config);

    .sioux-title {
      font: {
        size: mat-font-size($config, headline);
        weight: mat-font-weight($config, title);
      }
    }

    .sioux-card-header .sioux-card-header-titles {
      font-size: mat-font-size($config, title);
    }

    .sioux-card-header-sub-title,
    .sioux-card-content {
      font-size: mat-font-size($config, body-1);
    }

    .span,
    span,
    div {
      font-family: mat-font-family($config);
    }
  }
}

@mixin additionalFontSettings() {
  @include sioux-card-typography($config);
}

//style the scrollbar individually
@mixin scrollbar($primary, $secondary, $hover) {
  ::-webkit-scrollbar {
    //device with mouse
    @media (any-hover: hover) {
      width: $ScrollbarWidthMouse;
    }

    //touch device
    @media (any-hover: none), (any-hover: on-demand) {
      width: $ScrollbarWidthTouch;
    }

    background: $primary;
  }

  ::-webkit-scrollbar-thumb {
    -webkit-border-radius: 4px;
    border-radius: 4px;
    background: $secondary;

    &:hover {
      background: $hover !important;
    }
  }

  ::-webkit-scrollbar-track {
    background: $primary;
  }

  body {
    // For Internet Explorer
    scrollbar-face-color: $primary;
    // For Internet Explorer
    scrollbar-track-color: $secondary;

    overflow-y: scroll;
    overflow-x: hidden;
  }

  body {
    overflow-y: hidden;
    overflow-x: hidden;
  }
}

//set additionally shadow only in light theme
@mixin scrollbarShadow($primary, $secondary, $hover) {
  ::-webkit-scrollbar-thumb {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5) !important;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5) !important;
  }

  ::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3) !important;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3) !important;
  }
}

// create a personalized theme
@function gds-theme($primary, $accent, $warn, $foreground, $background, $IsDark) {
  @return (
    primary: $primary,
    accent: $accent,
    warn: $warn,
    is-dark: $IsDark,
    foreground: $foreground,
    background: $background
  );
}

$mat-ripple-color-opacity: 0.1;

@mixin mat-ripple() {
  // The host element of an mat-ripple directive should always have a position of "absolute" or
  // "relative" so that the ripples inside are correctly positioned relatively to the container.
  .mat-ripple {
    overflow: hidden;

    // By default, every ripple container should have position: relative in favor of creating an
    // easy API for developers using the MatRipple directive.
    position: relative;
  }

  .mat-ripple.mat-ripple-unbounded {
    overflow: visible;
  }

  .mat-ripple-element {
    position: absolute;
    border-radius: 50%;
    pointer-events: none;

    transition: opacity, transform 0ms cubic-bezier(0, 0, 0.2, 1);
    transform: scale(0);

    // In high contrast mode the ripple is opaque, causing it to obstruct the content.
    @include cdk-high-contrast {
      display: none;
    }
  }
}

/* Theme for the ripple elements.*/
@mixin mat-ripple-theme($theme) {
  $foreground: map_get($theme, foreground);
  $foreground-base: map_get($foreground, base);

  .mat-ripple-element {
    // If the ripple color is resolves to a color *type*, we can use it directly, otherwise
    // (e.g. it resolves to a CSS variable) we fall back to using the color and setting an opacity.
    @if (type-of($foreground-base) ==color) {
      background-color: rgba($foreground-base, $mat-ripple-color-opacity);
    } @else {
      background-color: $foreground-base;
      opacity: $mat-ripple-color-opacity;
    }
  }
}

body {
  font-family: $fontName;
  margin: 0;
}

*:focus {
  outline: none;
}

// --------- HTML -----------------------------------------------------------------
// definitions for apps like SICAM Grid Edge using html only without Angular and mat-classes
// up to now only for dark theme availabe
.html-h1 {
  font: 400 24px/36px Siemens Sans;
  margin: 0 0 16px;
  display: block;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0;
  margin-inline-end: 0;
}

@include mat-core($sioux-typography);
