//default
a {
  color: $TextAccentColor;
  background-color: transparent;
  text-decoration: none;
}

//hover
a:hover {
  color: $TextAccentColor;
  background-color: transparent;
  text-decoration: underline;
}

//visited
a:visited {
  color: $HyperlinkVisited;
  background-color: transparent;
  text-decoration: none;
}

//active
a:active {
  color: $AccentActivated;
  background-color: transparent;
  text-decoration: underline;
}

.mat-tab-nav-bar a {
  text-decoration: none;
  color: inherit;
}
