.mat-input-element {
  caret-color: $Text !important;
}

$WarningColor: mat-color($warn);

.mat-form-field-label {
  color: $TextSecondary;
}

.mat-hint {
  color: $Text;
}

.mat-form-field.mat-focused .mat-form-field-label {
  color: $TextAccentColor !important;

  &.mat-accent {
    color: $TextAccentColor !important;
  }

  &.mat-warn {
    color: $WarningColor !important;
  }
}

.mat-focused .mat-form-field-required-marker {
  color: $Text !important;
}

.mat-form-field-ripple {
  background-color: $Text;
}

.mat-form-field.mat-focused {
  .mat-form-field-ripple {
    background-color: $AccentColor !important;

    &.mat-accent {
      background-color: $AccentColor !important;
    }

    &.mat-warn {
      background-color: $WarningColor !important;
    }
  }
}

.mat-form-field-type-mat-native-select.mat-focused:not(.mat-form-field-invalid) {
  .mat-form-field-infix::after {
    color: $AccentColor;
  }

  &.mat-accent .mat-form-field-infix::after {
    color: $AccentColor;
  }

  &.mat-warn .mat-form-field-infix::after {
    color: $WarningColor;
  }
}

// Styling for the error state of the form field. Note that while the same can be
// achieved with the ng-* classes, we use this approach in order to ensure that the same
// logic is used to style the error state and to show the error messages.
.mat-form-field.mat-form-field-invalid {
  .mat-form-field-label {
    color: $WarningColor;

    &.mat-accent,
    .mat-form-field-required-marker {
      color: $WarningColor;
    }
  }

  .mat-form-field-ripple,
  .mat-form-field-ripple.mat-accent {
    background-color: $WarningColor !important;
  }
}

.mat-error {
  color: $WarningColor;
}

.mat-form-field-invalid .mat-form-field-underline::before {
  background-color: $WarningColor !important;
}

.mat-form-field.mat-focused.mat-form-field-invalid .mat-form-field-label {
  color: $WarningColor !important;
}

.mat-warn .mat-input-element,
.mat-form-field-invalid .mat-input-element {
  caret-color: $WarningColor !important;
}

.mat-form-field-type-mat-native-select.mat-form-field-invalid .mat-form-field-infix::after {
  color: $WarningColor !important;
}

.mat-input-element:disabled,
.mat-form-field-type-mat-native-select.mat-form-field-disabled .mat-form-field-infix::after {
  color: $TextDisabled !important;
}

.mat-form-field-underline {
  transform: none !important;
}

//on background and elevation 01 ---------------------------------------------------------------------
//text field
//change background color of a filled input field/and a drop-down
.mat-form-field-appearance-fill {
  .mat-form-field-flex {
    background-color: $Elevation08 !important;

    .mat-icon-button {
      &:hover {
        background-color: $HoverElevation16;
      }
    }
  }

  &.mat-form-field-disabled .mat-form-field-flex {
    background-color: $PrimaryButtonDisabled08;
  }

  .mat-form-field-underline::before {
    background-color: $TextSecondary;
  }

  &.mat-form-field-disabled {
    .mat-form-field-label {
      color: $TextDisabled;
    }

    .mat-form-field-underline::before {
      background-color: transparent;
    }
  }
}

//on navigation drawer/side sheet of type side/push ---------------------------------------------------------------------------------------------------------------
mat-sidenav-container>mat-sidenav.mat-drawer-side {
  .mat-form-field-appearance-fill {
    .mat-form-field-flex {

      background-color: $Elevation08 !important;

      .mat-icon-button {
        &:hover {
          background-color: $HoverElevation16;
        }
      }
    }
  }
}

//on navigation drawer/side sheet of type over----------------------------------------------------------------------------------------------------------------------------
mat-sidenav-container>mat-sidenav.mat-drawer-over {
  .mat-form-field-appearance-fill {
    .mat-form-field-flex {
      background-color: $ElevationOnDialog !important;

      .mat-icon-button {
        &:hover {
          background-color: $HoverElevationOnDialog;
        }
      }
    }
  }
}

//on dialog---------------------------------------------------------------------------------------------------------------------------------
.mat-dialog-content {
  .mat-form-field-appearance-fill {
    .mat-form-field-flex {
      background-color: $ElevationOnDialog !important;

      .mat-icon-button {
        &:hover {
          background-color: $HoverElevationOnDialog;
        }
      }
    }
  }
}
