//------- TABS -----------------------------------------------------------------
//Tabs on elevation 00/01
// selection bar on bottom of tab
.mat-tab-group.mat-primary .mat-ink-bar,
.mat-tab-nav-bar.mat-primary .mat-ink-bar {
  background-color: $AccentColor !important;
  color: $Text !important;
}

//activated tab
.mat-tab-label-active,
.mat-tab-label.mat-tab-label-active,
.mat-tab-link.mat-tab-label-active {
  background: $ActivatedElevation01 !important;
  color: $TextAccentColor !important;
  opacity: 1;
}

//hovered activated tab
.mat-tab-label.mat-tab-label.mat-ripple.mat-tab-label-active:hover,
.mat-tab-label.mat-tab-label.mat-ripple.mat-tab-label.mat-selected:hover,
.mat-tab-label.mat-tab-label-active:hover,
.mat-tab-label.mat-selected .mat-tab-label-active:hover,
.mat-tab-link.mat-tab-label-active:hover {
  background: $HoverActivatedElevation01 !important;
  color: $TextAccentColorOnActivatedElements !important;
}

// not activated tabs
.mat-tab-label,
.mat-tab-link {
  color: $TextSecondary !important;
  background: transparent !important;
  opacity: 1 !important;
}

//hovered not activated tabs
.mat-tab-label:hover,
.mat-tab-link:hover {
  opacity: 1;
  color: $Text !important;
  background: $HoverElevation01 !important;
}

//on navigation drawer/side sheet of type over----------------------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-over {
  //activated tab
  .mat-tab-label-active,
  .mat-tab-label.mat-tab-label-active,
  .mat-tab-link.mat-tab-label-active {
    background: $ActivatedElevation16 !important;
    color: $TextAccentColor !important;
    opacity: 1 !important;
  }

  //hovered activated tab
  .mat-tab-label.mat-tab-label.mat-ripple.mat-tab-label-active:hover,
  .mat-tab-label.mat-tab-label.mat-ripple.mat-tab-label.mat-selected:hover,
  .mat-tab-label.mat-tab-label-active:hover,
  .mat-tab-label.mat-selected .mat-tab-label-active:hover,
  .mat-tab-link.mat-tab-label-active:hover {
    background: $HoverActivatedElevation16 !important;
    color: $TextAccentColorOnActivatedElements !important;
  }

  //hovered not activated tabs
  .mat-tab-label:hover,
  .mat-tab-link:hover {
    opacity: 1;
    color: $Text !important;
    background: $HoverElevation16 !important;
  }
}

//on dialog---------------------------------------------------------------------------------------------------------------------------------
.mat-dialog-content {
  //activated tab
  .mat-tab-label-active,
  .mat-tab-label.mat-tab-label-active,
  .mat-tab-link.mat-tab-label-active {
    background: $ActivatedElevationOnDialog !important;
    color: $TextAccentColor !important;
    opacity: 1 !important;
  }

  //hovered activated tab
  .mat-tab-label.mat-tab-label.mat-ripple.mat-tab-label-active:hover,
  .mat-tab-label.mat-tab-label.mat-ripple.mat-tab-label.mat-selected:hover,
  .mat-tab-label.mat-tab-label-active:hover,
  .mat-tab-label.mat-selected .mat-tab-label-active:hover,
  .mat-tab-link.mat-tab-label-active:hover {
    background: $HoverActivatedElevationOnDialog !important;
    color: $TextAccentColorOnActivatedElements !important;
  }

  //hovered not activated tabs
  .mat-tab-label:hover,
  .mat-tab-link:hover {
    opacity: 1;
    color: $Text !important;
    background: $HoverElevationOnDialog !important;
  }
}
