//general settings ----------------------------------------------------------------------------

//definitions for icon in active list item
.mat-list-item:active > .mat-list-item-content > .mat-icon {
  color: $TextAccentColor !important;
}

//general drag item list
.mat-list-item.cdk-drag-preview {
  @include mat-elevation(8);
  background-color: $DraggedElevation01;
  box-sizing: border-box;
  border-radius: 4px;
}

.cdk-drag-placeholder {
  opacity: 0;
}

.cdk-drag-animating {
  transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
}

.mat-line {
  // all lines but the top line should have smaller text
  &:nth-child(n + 2) {
    color: $TextSecondary;
  }
}

//settings on background elevation 01 ----------------------------------------------------------

.mat-list-content {
  background-color: $Elevation01;
}

.list-item-active {
  background-color: $ActivatedElevation01 !important;
  color: $TextAccentColor !important;
}

//required in order to keep the active entry displayed as active in the navigation drawer
.active {
  background-color: $ActivatedElevation01 !important;
  color: $TextAccentColor !important;
}

.mat-list-item,
.mat-list-option,
.mat-nav-list .mat-list-item,
.mat-action-list .mat-list-item {
  background-color: $Elevation01;
  color: $Text;

  &:hover {
    background: $HoverElevation01;

    .mat-icon-button {
      &:hover {
        background-color: $HoverElevation08;
      }
    }
  }

  &:focus {
    background-color: $FocusedElevation01;
  }

  &:active {
    background-color: $ActivatedElevation01 !important;
    color: $TextAccentColor !important;
    width: 100%;
  }
}

.list-item-active {
  background-color: $ActivatedElevation01 !important;
  color: $TextAccentColor !important;
}

//required in order to keep the active entry displayed as active in the navigation drawer
.active {
  background-color: $ActivatedElevation01 !important;
  color: $TextAccentColor !important;
}

//List on a card
.mat-card {
  .mat-list-item,
  .mat-list-option,
  .mat-nav-list .mat-list-item,
  .mat-action-list .mat-list-item {
    background-color: $Elevation01;

    &:hover {
      background: $HoverElevation01;

      .mat-icon-button {
        &:hover {
          background-color: $HoverElevation08;
        }
      }
    }

    &:focus {
      background-color: $FocusedElevation01;
    }

    &:active {
      background-color: $ActivatedElevation01 !important;
      color: $TextAccentColor !important;
      width: 100%;
    }

    .mat-icon {
      color: $IconColor;
    }
  }
}

//general drag item list
.mat-list-item.cdk-drag-preview {
  background-color: $DraggedElevation01;
}

