.mat-selected:hover {
  opacity: 1;
  color: $Text;
}

.mat-selected:active {
  background: $ActivatedElevation01;
  opacity: 1;
  color: $TextAccentColor;
}

.mat-select.mat-select-invalid .mat-select-arrow {
  color: mat-color($warn);
}

.mat-form-field-appearance-fill {
  .mat-select-content,
  .mat-select-panel {
    background-color: $PrimaryButtonColor01 !important;

    .mat-icon-button {
      &:hover {
        background-color: $PrimaryButtonHover01;
      }
    }
  }
}

.mat-selected .mat-accent {
  background-color: $AccentSelected;
}

.mat-selected.mat-primary,
.mat-selected {
  background-color: $SelectedElevation01;
}

// mat-option
.mat-option {
  color: $Text;

  &:hover:not(.mat-option-disabled) {
    background: $HoverElevation01;
  }

  &:focus:not(.mat-option-disabled) {
    background: $FocusedElevation01;
  }

  // In multiple mode there is a checkbox to show that the option is selected.
  &.mat-selected:not(.mat-option-multiple):not(.mat-option-disabled) {
    background: $HoverElevation01;
  }

  &.mat-active {
    background: $ActivatedElevation01;
    color: $TextAccentColor;
  }

  &.mat-active:hover {
    background: $HoverActivatedElevation01;
    color: $TextAccentColor;
  }

  &.mat-option-disabled {
    color: $TextDisabled;
  }
}

.mat-primary .mat-option.mat-selected:not(.mat-option-disabled) {
  color: $TextAccentColor;
  background-color: $ActivatedElevation01;
}

//hovered activated element
.mat-primary .mat-option.mat-selected:not(.mat-option-disabled):hover {
  color: $TextAccentColorOnActivatedElements;
  background-color: $HoverActivatedElevation01;
}

.mat-accent .mat-option.mat-selected:not(.mat-option-disabled) {
  color: $AccentColor;
}

//on navigation drawer/side sheet of type side/push ---------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-side {
  .mat-form-field-appearance-fill {
    .mat-select-content,
    .mat-select-panel {
      background-color: $PrimaryButtonColor01 !important;

      .mat-icon-button {
        &:hover {
          background-color: $HoverElevation01;
        }
      }
    }
  }

  .mat-selected .mat-primary.mat-selected {
    background-color: $SelectedElevation01 !important;
    color: $AccentColor;
  }
}

//on navigation drawer/side sheet of type over----------------------------------------------------------------------------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-over {
  .mat-select-content,
  .mat-select-panel {
    background-color: $PrimaryButtonColorOnDialog !important;

    .mat-icon-button {
      &:hover {
        background-color: $PrimaryButtonHover01;
      }
    }
  }

  .mat-selected.mat-primary,
  .mat-selected {
    background-color: $SelectedElevation16 !important;
    color: $Text !important;
  }
}

//on dialog---------------------------------------------------------------------------------------------------------------------------------
.mat-dialog-content {
  .mat-select-content,
  .mat-select-panel {
    background-color: $PrimaryButtonColorOnDialog !important;

    .mat-icon-button {
      &:hover {
        background-color: $HoverElevationOnDialog;
      }
    }
  }

  .mat-selected.mat-primary,
  .mat-selected {
    background-color: $SelectedElevation24 !important;
    color: $Text !important;
  }
}
