//general card styles----------------------------------------------------------------

//Style of sioux_Card/card with hover
.sioux-card,
.CardWithHover {
  @include mat-elevation(1);
  background-color: $Elevation01;

  &:hover {
    @include mat-elevation(8);
    background-color: $HoverElevation08;
    cursor: pointer;

    .mat-icon-button {
      background-color: $HoverElevation16;
    }
  }
}

//dragged sioux-card, cardwithhover
.sioux-card.cdk-drag-dragging,
.CardWithHover.cdk-drag-dragging {
  @include elevation(8);
  background-color: $DraggedElevation08;
}

//------mat card----------------------------------------------------------------
.mat-card {
  @include elevation(1);
  background-color: $Elevation01;

  &:hover {
    cursor: pointer;
  }
}

.mat-card.cdk-drag-dragging {
  @include elevation(8);
  background-color: $DraggedElevation08;
}

//On a navigation drawer of type side---------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-side {
  //card with hover, sioux card
  .sioux-card,
  .CardWithHover {
    @include elevation(1);
    background-color: $Elevation01;

    &:hover {
      @include elevation(8);
      background-color: $HoverElevation08;
      cursor: pointer;

      .mat-icon-button {
        background-color: $HoverElevation16;
      }
    }

    &:focus {
      background-color: $FocusedElevation01;
      border: 1px solid $Text !important;
    }
  }

  .sioux-card.cdk-drag-dragging,
  .CardWithHover.cdk-drag-dragging {
    @include elevation(8);
    background-color: $DraggedElevation08;
  }

  //mat-card
  .mat-card {
    background-color: $Elevation01;

    &:focus {
      background-color: $FocusedElevation01;
      border: 1px solid $Text !important;
    }
  }

  .mat-card.cdk-drag-dragging {
    @include elevation(8);
    background-color: $DraggedElevation08;
  }
}

//On a navigation drawer of type over-------------------------------------------------------
mat-sidenav-container > mat-sidenav.mat-drawer-over {
  //card with hover, sioux card
  .sioux-card,
  .CardWithHover {
    @include elevation(1);
    background-color: $Elevation16;

    &:hover {
      @include elevation(8);
      background-color: $HoverElevation16;
      cursor: pointer;

      .mat-icon-button {
        background-color: $HoverElevation16;
      }
    }

    &:focus {
      background-color: $FocusedElevation16;
      border: 1px solid $Text !important;
    }
  }

  .sioux-card.cdk-drag-dragging,
  .CardWithHover.cdk-drag-dragging {
    @include elevation(8);
    background-color: $DraggedElevation24;
  }

  //mat card
  .mat-card {
    @include elevation(1);
    background-color: $Elevation16;

    &:focus {
      background-color: $FocusedElevation16;
      border: 1px solid $Text !important;
    }
  }

  .mat-card.cdk-drag-dragging {
    @include elevation(8);
    background-color: $DraggedElevation24;
  }
}

//on dialog---------------------------------------------------------------------------------------------------------------------------------
.mat-dialog-content {
  //card with hover, sioux card
  .sioux-card,
  .CardWithHover {
    @include elevation(1);
    background-color: $ElevationOnDialog;

    &:hover {
      @include elevation(8);
      background-color: $HoverElevationOnDialog;
      cursor: pointer;

      .mat-icon-button {
        background-color: $HoverElevationOnDialog;
      }
    }

    &:focus {
      background-color: $FocusedElevationOnDialog;
      border: 1px solid $Text !important;
    }
  }

  .sioux-card.cdk-drag-dragging,
  .CardWithHover.cdk-drag-dragging {
    @include mat-elevation(8);
    background-color: $DraggedElevationOnDialog;
  }

  //mat-card
  .mat-card {
    @include elevation(1);
    background-color: $ElevationOnDialog;

    &:focus {
      background-color: $FocusedElevationOnDialog;
      border: 1px solid $Text !important;
    }
  }

  .mat-card.cdk-drag-dragging {
    @include mat-elevation(8);
    background-color: $DraggedElevationOnDialog;
  }
}
