//Datepicker ----------------------------------------------------------------------
// The prev/next buttons need a bit more specificity to
// avoid being overwritten by the .mat-icon-button.
.mat-datepicker-toggle,
.mat-datepicker-content .mat-calendar-next-button,
.mat-datepicker-content .mat-calendar-previous-button {
  color: $Text;
}

.mat-datepicker-content {
  background-color: $Elevation01;
  color: $TextAccentColor;
}

//calender icon when open
.mat-datepicker-toggle-active {
  color: $Text;
}

//Calender settings ---------------------------------------------------------------------------------
.mat-calendar {
  background-color: $ElevationOnDialog;
}

.mat-calendar-arrow {
  border-top-color: $Text;
}

.mat-calendar-table-header {
  color: $TextSecondary;
}

.mat-calendar-table-header-divider::after {
  background: divider;
}

.mat-calendar-body-label {
  color: $TextSecondary;
}

.mat-calendar-body-cell-content {
  color: $Text;
  border-color: transparent;

  &:hover {
    background-color: $HoverElevationOnDialog !important;
  }
}

.mat-calendar-body-disabled > .mat-calendar-body-cell-content:not(.mat-calendar-body-selected) {
  color: $TextDisabled;
}

.mat-calendar-body-today:not(.mat-calendar-body-selected) {
  border-color: $Text;
  background-color: $ElevationOnDialog !important;
}

.mat-calendar-body-disabled > .mat-calendar-body-today:not(.mat-calendar-body-selected) {
  $color: $TextDisabled;
  background-color: $HeaderOnDialog !important;
}

.mat-calendar-body-cell {
  .mat-calendar-body-selected {
    background-color: $AccentSelected;
    color: $TextLight;
  }
}

.mat-calendar-cell-semi-selected {
  background-color: $TextAccentColor;
  color: $Text;
}

.mat-calendar-body-begin-range:not(.mat-calendar-body-end-range) {
  border-radius: 100% 0 0 100%;
  background-color: $TextAccentColor;
  color: $Text;
}

.mat-calendar-body-end-range:not(.mat-calendar-body-begin-range) {
  border-radius: 0 100% 100% 0;
  background-color: $TextAccentColor;
  color: $Text;
}

.mat-calendar-body-cell:not(.mat-calendar-body-disabled):hover,
.cdk-keyboard-focused .mat-calendar-body-active,
.cdk-program-focused .mat-calendar-body-active {
  > .mat-calendar-body-cell-content:not(.mat-calendar-body-selected) {
    background-color: $HoverElevationOnDialog;
  }
}

//Switch date time picker------------------------------------------------------------------------------
.date-selection-duration {
  .mat-button-toggle {
    background-color: $Elevation24 !important;
    color: $Text;
  }
}

.date-selection-duration {
  background-color: $Elevation24 !important;

  .mat-button-toggle-appearance-standard {
    background-color: $Elevation24 !important;
  }
}
