.mat-button-toggle-group.mat-button-toggle-group-appearance-standard {
  background-color: $PrimaryButtonColor01;
  border-radius: 36px;
  color: $Text;
  height: 36px;
}

.mat-button-toggle {
  color: $Text;
  border-radius: 36px;
  min-width: 64px;
  height: 36px;
}

.mat-button-toggle-appearance-standard .mat-button-toggle-label-content {
  line-height: 36px !important;
}

.mat-button-toggle:hover {
  background-color: $PrimaryButtonHover01;
}

.mat-button-toggle-group-appearance-standard .mat-button-toggle + .mat-button-toggle {
  border-left: solid 1px transparent;
}

[dir='rtl'] .mat-button-toggle-group-appearance-standard .mat-button-toggle + .mat-button-toggle {
  border-left: 0;
  border-right: solid 1px transparent;
}

.mat-button-toggle-group-appearance-standard.mat-button-toggle-vertical {
  .mat-button-toggle + .mat-button-toggle {
    border-left: 0;
    border-right: 0;
    border-top: solid 1px transparent;
  }
}

//checked
.mat-button-toggle-group .mat-button-toggle.mat-button-toggle-checked {
  background-color: $AccentColor;
  color: $Text;

  &.mat-button-toggle-appearance-standard {
    color: $TextLight;
  }
}

//checked
.mat-button-toggle-group .mat-button-toggle.mat-button-toggle-checked:hover {
  background-color: $AccentHover;
  color: $TextLight;
}

//disabled
.mat-button-toggle-group .mat-button-toggle.mat-button-toggle-disabled {
  color: $TextDisabled;
  background-color: $PrimaryButtonDisabled01;

  &:hover {
    background-color: $PrimaryButtonDisabled01;
  }

  &.mat-button-toggle-appearance-standard {
    background: $PrimaryButtonDisabled01;
  }

  &.mat-button-toggle-checked {
    background-color: $AccentDisabled;

    &:hover {
      background-color: $AccentDisabled;
    }
  }
}

.mat-button-toggle-standalone.mat-button-toggle-appearance-standard,
.mat-button-toggle-group-appearance-standard {
  border: solid 1px transparent;
}

//On a toolbar---------------------------------------------------
.mat-toolbar {
  .mat-button-toggle-group.mat-button-toggle-group-appearance-standard {
    background-color: $PrimaryButtonColor04;
  }

  .mat-button-toggle:hover {
    background-color: $PrimaryButtonHover04;
  }

  // background color of standard toggle button
  .mat-button-toggle-appearance-standard {
    color: $Text;
    background: $PrimaryButtonColor04;

    //disabled
    .mat-button-toggle-disabled {
      color: $TextDisabled;
      background-color: $PrimaryButtonDisabled04;

      &.mat-button-toggle-appearance-standard {
        background: $PrimaryButtonDisabled04;
      }
    }
  }
}

